import React, {Component} from "react";
import {connect} from "react-redux";
import {withRouter, NavLink} from "react-router-dom";
import {UIAction} from "../../Modules/UI";
import NavBar from "../../Components/NavBar";
import DesktopView from "../../Components/DesktopView";
import MobileView from "../../Components/MobileView";
import BrandTitle from "../../Components/BrandTitle";
import NavItem from "../../Components/NavLink";
import logo from '../../Assets/img/logo.svg';

class Nav extends Component {
  render() {
    const {Language, dispatch} = this.props;
    return (
      <NavBar className="row">
        <BrandTitle to="/" className="col-6 col-md-2">
          {logo
            ? <img src={logo} className="App-logo" alt="logo"/>
          :'AS'
}
        </BrandTitle>
        <DesktopView className="col-sm col-md">
          <NavItem>
            <ul>
              <li>
                <NavLink to="/">Portfolio</NavLink>
              </li>
              <li>
                <NavLink to="/skills">Skills</NavLink>
              </li>
              <li>
                <NavLink to="/about">Contact</NavLink>
              </li>
              <li>
{Language}
    <div className="dropdown-content">
      <span onClick={e => {
          dispatch(UIAction({type: "CHANGE_LANG", payload: "Fr"}));
        }}>Fr</span>
      <span onClick={e => {
          dispatch(UIAction({type: "CHANGE_LANG", payload: "En"}));
        }}>En</span>
    </div>
</li>
            </ul>
          </NavItem>
        </DesktopView>
        <MobileView className="col">
          test
        </MobileView>
      </NavBar>
    );
  }
}
const mapStateToProps = state => {
  return {Language: state.UI.Language};
};

export default withRouter(connect(mapStateToProps)(Nav));



