import styled from 'styled-components';

export default styled.div `
background:rgba(6,6,6,.38);
height:100%;
width:100%;
padding:10vh 0rem;

h1{
  margin-bottom:1rem;
  margin-top:1rem;
}

@media (min-width: 670px) {

  align-content: center;
  h1{
    margin-bottom:10rem;
  }
}



`



