import React, {Component, Children, Fragment} from 'react';
import Main from '../../Components/Card'

export class Title extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    const {children, current} = this.props
    const prev = (current === 0)
      ? Children.toArray(children).length - 1
      : current - 1;
    const next = (current === 2)
      ? 0
      : current + 1;

    return (<Fragment>
      <div className="col-12 col-md-4" style={{
          textAlign: 'center'
        }}><h2>{Children.toArray(children)[prev]}</h2>
      </div>
      <div className="col-12 col-md-4" style={{
          textAlign: 'center'
        }}><h1>{Children.toArray(children)[current]}</h1>
      </div>
      <div className="col-12 col-md-4" style={{
          textAlign: 'center'
        }}><h2>{Children.toArray(children)[next]}</h2>
      </div>
    </Fragment>);
  }

}


const Frontend = () => (
    <div className="card">
      <p>
        Matre Front, sur un arbre perch, Tenait en son bec un fromage. Matre Renard, par lodeur allch, Lui tint  peu prs ce langage :  H ! bonjour, Monsieur du Corbeau. Que vous tes joli ! Que vous me semblez beau ! Sans mentir, si votre ramage Se rapporte  votre plumage, Vous tes le Phnix des htes de ces bois.  A ces mots le Corbeau ne se sent pas de joie ; Et pour montrer sa belle voix, Il ouvre un large bec, laisse tomber sa proie. Le Renard sen saisit, et dit :  Mon bon Monsieur, Apprenez que tout flatteur Vit aux dpens de celui qui lcoute : Cette leon vaut bien un fromage, sans doute.  Le Corbeau, honteux et confus, Jura, mais un peu tard, quon ne ly prendrait plus.</p>
  </div>);

